import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

import { NewService } from '../../angular-services/new.service';
import { NewCategoryService } from '../../angular-services/new-category.service';

@Component({
  selector: 'app-new-news-form-admin',
  templateUrl: './new-news-form.component.html',
  styleUrls: ['./new-news-form.component.css']
})
export class NewNewsFormComponent implements OnInit {

  formNewNews: FormGroup;
  alert: any = { type: '', message: '', show: false };
  categories: string[] = [];
  mediaType = 'news';
  urlObjects: string = 'https://viruscursinho.com.br:3002/news';
  showModalSelectFile: boolean = false;

  constructor(private newService: NewService, private categoryService: NewCategoryService) { }

  ngOnInit() {
    this.initForm();
    this.getCategories();
  }

  initForm() {
    this.formNewNews = new FormGroup({
      title: new FormControl('', Validators.required),
      content: new FormControl('', Validators.required),
      photo: new FormControl('', Validators.required),
      idCategory: new FormControl('', Validators.required),
      status: new FormControl('', Validators.required),
    });
  }

  save() {
    if (this.formNewNews.valid) {
      let data = this.formNewNews.value;
      if (data.status === "true") {
        data.status = true;
      } else {
        data.status = false;
      }
      this.newService.addNew(data).subscribe(
        response => {
          if (response !== undefined) {
            this.alert.type = 'success';
            this.alert.message = 'Dados salvos com sucesso!';
            this.alert.show = true;
          } else {
            this.alert.type = 'danger';
            this.alert.message = 'Houve algum erro ao salvar os dados!';
            this.alert.show = true;
          }
        }
      )
    }
  }

  getCategories() {
    this.categoryService.getCategories().subscribe(
      response => {
        if (response !== undefined) {
          for (let category of response) {
            if (category.status) {
              this.categories.push(category);
            }
          }
        } else {
          this.alert.type = 'danger';
          this.alert.message = 'Houve algum erro ao salvar os dados!';
          this.alert.show = true;
        }
      }
    )
  }

  changeStatusModalSelectFile() {
    if (this.showModalSelectFile) {
      this.showModalSelectFile = false;
    } else {
      this.showModalSelectFile = true;
    }
  }

  setUrlObject(link) {
    this.formNewNews.patchValue({
      photo: link
    });
    this.showModalSelectFile = false;
  }

}
