import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { NewNewsPageComponent } from './new-news-page.component';

const routes: Routes = [
  { path: '', component: NewNewsPageComponent }
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class NewNewsPageRoutingModule { }
